.DrawerToggle {
	width: 40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column;
	    flex-flow: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	cursor: pointer;
	position: absolute;
	top: 17px;
	left: 17px;
}

.DrawerToggle div {
	width: 70%;
	height: 3px;
	background-color: white;
	border-radius: 2px;
	margin: 3px;
}

@media (min-width: 780px) {
	.DrawerToggle {
		display: none;
	}
}



