.PasswordChangeCard {
	margin: 0 auto 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.PasswordChangeCard h5 {
	margin: 20px 0 0;
	padding: 0;
}

.PasswordChangeCard button {
	padding-left: 30px;
	padding-right: 30px;
}

.PasswordChangeCardError {
	margin: 0 20px 10px;
}

.PasswordChangeCardError p {
	display: block;
	font-size: 1.1rem;
	font-weight: 400;
	line-height: 1.4;
	white-space: normal;
}



