.EmptyPage {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 0;
	text-align: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-line-pack: justify;
	    align-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	color: white;
	background-color: #006064;
}

.EmptyPage h3 {
	margin: 0 0 20px;
}

.EmptyPage a {
	color: orange;
}



