.NoteTitle {
	padding: 0;
	margin-right: 75px;
	margin-bottom: 15px;
}

.NotesAddNew {
	padding: 20px 20px 20px 30px;
}

.NotesAddNew h4 {
	margin: 0 0 20px;
	padding: 0;
}

.NotesAddNew input {
	margin: 0 0 10px;
}

.NotesAddNew .InputTags {
	margin: 10px 0;
}

.TagBadge {
	display: inline-block;
	padding: 5px 6px 6px;
	margin: 0 5px 5px 0;
	font-size: .7em;
	font-weight: 400;
	background-color: #999999;
	cursor: pointer;
}

.TagBadge:hover {
	background-color: #00796b;
}

.IconTag {
	font-size: .7em;
}

.NoteWrap{
	position: relative;
	border: 1px solid rgba(0,0,0,.125);
}

.NoteWrapTop {
	display: none;
	height: 20px;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 50;
	background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.71) 55%,rgba(255,255,255,0) 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,1)),color-stop(1%, rgba(255,255,255,0.99)),color-stop(55%, rgba(255,255,255,0.71)),to(rgba(255,255,255,0)));
	background: -o-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.71) 55%,rgba(255,255,255,0) 100%);
	background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.71) 55%,rgba(255,255,255,0) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 );
}

.NoteWrapBottom {
	display: none;
	height: 20px;
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 50;
	background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,0.71) 45%,rgba(255,255,255,0.99) 99%,rgba(255,255,255,1) 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0)),color-stop(45%, rgba(255,255,255,0.71)),color-stop(99%, rgba(255,255,255,0.99)),to(rgba(255,255,255,1)));
	background: -o-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,0.71) 45%,rgba(255,255,255,0.99) 99%,rgba(255,255,255,1) 100%);
	background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0.71) 45%,rgba(255,255,255,0.99) 99%,rgba(255,255,255,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 );
}

.NoteWrap:hover .NoteWrapTop {
	display: block;
}

.NoteWrap:hover .NoteWrapBottom {
	display: block;
}

.Icons {
	display: block;
	position: absolute;
	top: 0;
	right: 1.5rem
}

.IconSave {
	display: inline-block;
	font-size: 1.2em;
	margin-right: 5px;
	color: #adadad;
	cursor: pointer;
	background: none;
	border: none;
}

.IconReturn {
	display: inline-block;
	font-size: 1.1em;
	color: #adadad;
	cursor: pointer;
}

.IconSave:hover {
	color: #006064;
}

.IconReturn:hover {
	color: #006064;
}



