.RssReader {
	padding: 30px 30px 50px;
}

.RssReaderView {
	padding: 20px;
}

.RssReaderFull {
	position: absolute;
	top: 20px;
	left: 30px;
	right: 30px;
	bottom: 0;
	background-color: white;
}

.HeaderTitle {
	padding: 0;
	margin-right: 75px;
	margin-bottom: 15px;
}

.Images {
	display: block;
	max-width: 250px;
	margin-right: 20px;
	margin-bottom: 10px;
	float: left;
	padding: 10px; border: 1px solid #e4e4e4;
}

.Title {
	margin: 0 0 5px;
	line-height: 1em !important;
	padding: 0;
}

.Title a {
	font-size: 1.1rem;
	color: #006064;
	margin: 0;
	padding: 0;
	text-decoration: none;
}

.Title a:hover {
	color: #3b847d;
}

.Amount {
	font-size: .8em;
	font-weight: normal;
	color: #80cbc4;
}

.LinkItemIcon {
	font-size: .8em;
	color: #80cbc4;
}

.Title a:hover .LinkItemIcon {
	color: #006064;
}

.Categories {
	font-size: .8em;
	font-weight: bold;
	margin: 0 0 8px;
	padding: 0;
	color: #3b847d;
}

.Date {
	font-size: .8em;
	margin: 0 0 8px;
	padding: 0;
	color: #3b847d;
}

.Author {
	font-size: .8em;
	margin: 0 0 8px;
	padding: 0;
	color: #3b847d;
}

.Content {
	font-size: 1em;
	line-height: 1.2em;
	margin: 0;
	padding: 0;
	display: block;
}

.Content ol {
	 margin: 0 0 0 245px;
}

.Content ul {
	margin: 0 0 0 245px;
}

.Content img {
	max-width: 100%;
}

.Clear {
	clear: both;
}

.RssReaderListWrap{
	position: relative;
	border: 1px solid rgba(0,0,0,.125);
}

.RssReaderListWrapTop {
	display: none;
	height: 20px;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 50;
	background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.71) 55%,rgba(255,255,255,0) 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,1)),color-stop(1%, rgba(255,255,255,0.99)),color-stop(55%, rgba(255,255,255,0.71)),to(rgba(255,255,255,0)));
	background: -o-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.71) 55%,rgba(255,255,255,0) 100%);
	background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0.99) 1%,rgba(255,255,255,0.71) 55%,rgba(255,255,255,0) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 );
}

.RssReaderListWrapBottom {
	display: none;
	height: 20px;
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 50;
	background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,0.71) 45%,rgba(255,255,255,0.99) 99%,rgba(255,255,255,1) 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0)),color-stop(45%, rgba(255,255,255,0.71)),color-stop(99%, rgba(255,255,255,0.99)),to(rgba(255,255,255,1)));
	background: -o-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,0.71) 45%,rgba(255,255,255,0.99) 99%,rgba(255,255,255,1) 100%);
	background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0.71) 45%,rgba(255,255,255,0.99) 99%,rgba(255,255,255,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 );
}

.RssReaderListWrap:hover .RssReaderListWrapTop {
	display: block;
}

.RssReaderListWrap:hover .RssReaderListWrapBottom {
	display: block;
}

.ListEmpty {
	text-align: center;
	font-size: 1em;
	color: #999999;
}


.FeedsIcons {
	position: absolute;
	top: 0;
	right: 20px;
}

.RssReaderFull .FeedsIcons {
	top: 1px;
	right: 5px;
}

.ReloadIcon {
	cursor: pointer;
	font-size: 1.1em;
	color: #80cbc4;
	margin-right: 5px;
}

.ReloadIcon:hover {
	color: #006064;
}

.RemoveIcon {
	cursor: pointer;
	font-size: 1.1em;
	color: #80cbc4;
}

.RemoveIcon:hover {
	color: #006064;
}

.ExpandIcons {
	display: none;
	padding: 0 4px;
	position: absolute;
	bottom: 10px;
	right: 10px;
	z-index: 100;
	background: rgba(255,255,255, .6);
	border-radius: 6px;
	border: 1px solid #e4e4e4;
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
}

.RssReaderListWrap:hover .ExpandIcons {
	display: block;
}

.ExpandIcon {
	cursor: pointer;
	font-size: 1.1em;
	color: #80cbc4;
}

.ExpandIcon:hover {
	color: #006064;
}



